import ClickOutside from '../directives/ClickOutside.js'
import Resize from '../directives/Resize.js'

export default {
  directives: {ClickOutside, Resize},
  props: {
    listClass: {
      type: String,
      default: ''
    },
    mode: {
      type: String,
      default: 'click' // hover
    },
    autoHide: {
      type: Boolean,
      default: true
    },
    maxHeight: {
      type: String,
      default: '180'
    }
  },
  data: () => ({
    popperShow: false
  }),
  methods: {
    show () {
      if (this.timeout) clearTimeout(this.timeout)
      this.popperVm.actionPopper(true)
    },
    hide () {
      this.timeout = setTimeout(_ => {
        this.popperVm.actionPopper(false)
        clearTimeout(this.timeout)
        this.timeout = null
      }, 50)
    },
    handleResize () {
      if (this.$el) this.minwidth = this.$el.clientWidth
      this.popperVm.updatePopper()
    },
    clickHandle () {
      this.popperVm.actionPopper()
    },
    closeHandle () {
      if (this.mode === 'hover') return
      this.popperVm.actionPopper(false)
    },
    initEvent () {
      let {mode, show, hide, clickHandle} = this
      const eventElm = this.$el
      const popperElm = this.popperElm

      this.popperVm.$on('statusChange', this.statusChange)

      if (mode === 'hover') {
        eventElm.addEventListener('mouseenter', show)
        eventElm.addEventListener('mouseleave', hide)

        popperElm.addEventListener('mouseenter', show)
        popperElm.addEventListener('mouseleave', hide)
      } else if (mode === 'click') {
        eventElm.addEventListener('click', clickHandle)
      }
    },
    itemSelect (d) {
      if (d === undefined) {
        console.log('m-list-item props.dataPost 没有设置')
      } else {
        if (this.$options.name === 'MSelect') {
          this.$emit('input', d)
          this.selfvalue = d
        }  // 只有Mselect 开启v-model支持
      }
      if (this.autoHide) this.hide()
    },
    initStyle () {
      const refw = this.$el.clientWidth
      const popw = this.popperElm.clientWidth
      if (popw > refw) {
        delete this.popperElm.style.width
        this.popperElm.style.minWidth = refw + 'px'
      } else {
        delete this.popperElm.style.minWidth
        this.popperElm.style.width = refw + 'px'
      }
      this.popperVm.updatePopper()
    },
    statusChange (bl) {
      if (bl) {
        const timeout = setTimeout(_ => {
          this.initStyle()
          clearTimeout(timeout)
        }, 10)
      }
    }
  },
  mounted () {
    this.popperVm = this.$refs.popper || this.$slots.dropdown[0].child
    this.popperVm.reference = this.$el
    this.popperElm = this.popperVm.$el

    if (this.maxHeight) this.popperElm.style.maxHeight = this.maxHeight + 'px'

    this.initEvent()
  },
  computed: {
    stateClass () {
      return this.popperShow ? 'active' : ''
    }
  }
}



