import Tool from '../utils/Tool'

class PopManage {
  constructor () {
    this.pops = {} // 存储pop实例
    this.zIndex = 999 // index
    this.pops = []
  }
  register (el) {
    let id = el.getAttribute('data-pop-id')
    if (!id) {
      id = Tool.unid()
      el.setAttribute('data-pop-id', id)
      el.style.zIndex = this.zIndex++
    }
    if (this.pops.indexOf(id) === -1) {
      this.pops.push(id)
    } else {
      console.log(id + '已经存在')
    }
  }
  cancel (el) {
    let id = el.getAttribute('data-pop-id')
    if (id) {
      this.pops.splice(this.pops.indexOf(id), 1)
    }
  }
}

export default new PopManage()



