import Button from './components/button' // 按钮
import Checkbox from './components/checkbox' // 复选框
import Radio from './components/radio' // 单选
import Input from './components/input' // 输入框
import Textarea from './components/textarea' // 输入框
import FileUp from './components/fileup' // 文件上传
import Select from './components/select' // 选择框
import List from './components/list' // 列表框
import Dropdown from './components/dropdown' // 列表框
import Tree from './components/tree' // 树形结构
import Pages from './components/pages' // 分页
import Table from './components/table' // 表格
import Tip from './components/tip' // 提示
import Toast from './components/toast' // toast
import Grid from './components/grid' // 网格
import Icon from './components/icon' // 图标 采用iconfont的字体库 请在页面link引入
import Message from './components/message' // 提示框 互动框
import Scroller from './components/scroller' // 滚动体
import Tabs from './components/tabs' // 标签选项卡

import Waves from './components/waves/' // 水波纹效果
// 引入核心样式
import './stylesheets/core.scss'

const options = { Button, Checkbox, Select, Radio, Input, Textarea, FileUp, List, Dropdown, Tree, Pages, Table, Scroller, Grid, Waves, Icon, Tabs }

options.install = (Vue) => {
  for (let component in options) {
    const componentInstaller = options[component]
    if (componentInstaller && component !== 'install') {
      Vue.use(componentInstaller)
    }
  }

  Vue.prototype.$tip = Tip
  Vue.prototype.$toast = Toast
  Vue.prototype.$message = Message
}

export default options



